footer{

	border-top: 0 !important;

	.col{
		border-top: 1px dashed $__border--light; 
		padding: 15px 0;

		@include __on-theme--dark {
			border-color: $__border--dark; 
		}
		
		@include __on-theme--light {
			border-color: $__border--light; 
		}
	}

	p{
		margin: 0;
	}

	.address-scielo{
		background-color: $__bg__gray--1;

		@include __on-theme--dark {
			background-color: $__bg__white--1;
		}
		
		@include __on-theme--light {
			background-color: $__bg__gray--1;
		}

		.col{
			border: 0;

			@include media-breakpoint-only(xs) {
				padding-left: 8px;
				padding-right: 8px;

				&:first-child{
					padding-bottom:0;
				}
				&:last-child{
					padding-top:0;
				}
			}
		}
	}

	.partners{
		padding: 16px 0;

		img{
			@include media-breakpoint-only(xs) {
				margin: 8px 0;
			}	
		}
	}
}
