/*
Logo SciELO
*/
.#{$__classPrefix}__logo-scielo{
	background-image: url(../img/logo-scielo-no-label.svg);
	background-size: contain;
	background-repeat: no-repeat;	
	display: inline-block;
    width: __remcalc(50px);
    height: __remcalc(50px);

    @include __on-theme--dark {
		background-image: url(../img/logo-scielo-no-label-negative.svg);
	}
	
	@include __on-theme--light {
		background-image: url(../img/logo-scielo-no-label.svg);
	}



	&--large {
        background-image: url(../img/logo-scielo-no-label.svg);
        background-size: contain;
	    background-repeat: no-repeat;
        display: inline-block;
        width: __remcalc(250px);
        height: __remcalc(250px);

        @include __on-theme--dark {
            background-image: url(../img/logo-scielo-no-label-negative.svg);
        }
        
        @include __on-theme--light {
            background-image: url(../img/logo-scielo-no-label.svg);
        }

    }
	&--medium {
        background-image: url(../img/logo-scielo-no-label.svg);
        background-size: contain;
	    background-repeat: no-repeat;
        display: inline-block;
        width: __remcalc(150px);
        height: __remcalc(150px);

        @include __on-theme--dark {
            background-image: url(../img/logo-scielo-no-label-negative.svg);
        }
        
        @include __on-theme--light {
            background-image: url(../img/logo-scielo-no-label.svg);
        }

    }
	&--small{
        background-image: url(../img/logo-scielo-no-label.svg);
        background-size: contain;
	    background-repeat: no-repeat;
        display: inline-block;
        width: __remcalc(50px);
        height: __remcalc(50px);

        @include __on-theme--dark {
            background-image: url(../img/logo-scielo-no-label-negative.svg);
        }
        
        @include __on-theme--light {
            background-image: url(../img/logo-scielo-no-label.svg);
        }
    }
    

	footer &,
	header &{
		width: 50px;
		height: 50px;
	}

}
